import React from 'react';
import './index.css';
import './index.scss';
import Link from 'gatsby-link'
import kebabCase from "lodash/kebabCase";

const getClass = () => {
    var defualtClass = "button center"
    var colorClass = [" -green", " -blue", " -salmon", " -sun", " -alge"];
    var rand = colorClass[Math.floor(Math.random() * colorClass.length)];
    var returnClass = defualtClass + rand
    return returnClass;
}

export const TagButton = ({text}) => (
    <Link className="button-link" to={`/tags/${kebabCase(text)}/`}>
        <div className={getClass()}>{text}</div>
    </Link>
)


